.overlayHW2 {
    width: 30%;
    display: flex;
    flex-direction: column;
    margin: 10px 25px;
}

.overlayAffairs {
    width: 100%;
    display: flex;
    flex-direction: column;
}

.overlayAffair {
    display: flex;
    justify-content: space-between;
    margin: 5px 0;
    border-bottom: 1px dotted #3a33d1;
}

.overlayButton {
    display: flex;
    margin: 10px 0;
}
.overlayButton > button {
    margin: 0 5px;
}

.nameAffair {
    color: #3a33d1;
}
.affairPriority {
    color: lightcoral;
}
span {
    margin: 0 10px;
    color: #ffffff
}

